







































import Vue from 'vue';
import BackButton from '@/common/components/BackButton.vue';
import Layout from '@/common/components/Layout.vue';
import HeightContainer from '@/configurator/components/utils/HeightContainer.vue';
import InteractionsContainer from '@/configurator/components/utils/InteractionsContainer.vue';
import DeleteButton from '@/configurator/components/utils/DeleteButton.vue';
import SelectionButtons from '@/common/components/SelectionButtons.vue';
import SelectionPreview from '@/planner/components/SelectionPreview.vue';
import PlannerSidebar from '@/planner/components/PlannerSidebar.vue';
import {PLANNER_UI_STATE_MUTATIONS} from '@/planner/store/planner-ui-state';
import RotateButton from '@/planner/components/RotateButton.vue';
import BottomBar from '@/planner/components/BottomBar.vue';
const DEFAULT_ROTATION_VALUE = Math.PI / 8 /* rotate 22.5deg, see: https://www.mathsisfun.com/geometry/radians.html */;
export default Vue.extend({
  components: {
    BackButton,
    InteractionsContainer,
    HeightContainer,
    Layout,
    DeleteButton,
    SelectionButtons,
    SelectionPreview,
    PlannerSidebar,
    RotateButton,
    StartConfiguringButton: () => import('@/configurator/components/utils/StartConfiguringButton.vue'),
    BottomBar,
  },
  data() {
    return {
      isLoadingCatalog: true,
    };
  },
  async mounted() {
    const tagId = this.$store.state.uiState.initData?.catalogRootTag;
    if (!tagId) {
      this.isLoadingCatalog = false;
      return;
    }
    try {
      this.isLoadingCatalog = true;
      const rapiAccess = await this.$sdkConnector.getRapiAccess();
      const rootTag = await rapiAccess.getTag(tagId);
      const tagIds = rootTag.tags;

      const tags = await Promise.all(tagIds.map((id) => rapiAccess.getTag(id)));
      const tagsWithItems = await Promise.all(tags.map((tag) => {
        return new Promise<any>(async (resolve) => {
          const items = await rapiAccess.getItemsOf(tag);
          tag = JSON.parse(JSON.stringify(tag));
          (tag as any).items = items;
          resolve(tag);
        });
      }));
      this.$store.commit(PLANNER_UI_STATE_MUTATIONS.SET_CATALOG_ROOT_TAG, tagsWithItems);
      this.$store.commit(PLANNER_UI_STATE_MUTATIONS.SET_SHOW_CATALOG, true);
      this.isLoadingCatalog = false;
    } catch (e) {
      this.isLoadingCatalog = false;
    }
  },
  methods: {
    async deleteSelection() {
      const api = await this.$sdkConnector.plannerApi;
      api.deleteSelection();
    },
    async rotateSelection() {
      const api = await this.$sdkConnector.plannerApi;
      api.rotateSelection(DEFAULT_ROTATION_VALUE);
    },
  },
});
