



























import Vue from 'vue';
import CollectionView from '@/configurator/components/collection-view/CollectionView.vue';
import {CollectionViewElement} from '@/configurator/components/collection-view/-utils/types';
import {PLANNER_UI_STATE_ACTIONS, PLANNER_UI_STATE_GETTERS} from '@/planner/store/planner-ui-state';
import {Nullable} from '@/common/utils/types';
import {COLLECTION_VIEW_STATE_MUTATIONS} from '@/common/store/collection-view-state';
import Spinner from '@/configurator/components/utils/Spinner.vue';
import Welcome from '@/planner/components/Welcome.vue';
export default Vue.extend({
  components: {
    CollectionView,
    Spinner,
    Welcome,
  },

  props: {
    onToggleExpand: Function,
    isLoading: Boolean,
  },

  computed: {
    elements(): CollectionViewElement[] {
      if (this.$store.state.plannerUiState.showCatalog) {
        return this.$store.getters[PLANNER_UI_STATE_GETTERS.CATALOG_ROOT_TAG] as CollectionViewElement[] || [];
      }
      return [];
    },
    helpText(): Nullable<string> {
      if (!this.$store.state.plannerUiState.isDesktop) {
        return null;
      }
      return 'Catalog';
      // FIXME
      // return (this as any).t.t('params.no-elements');
    },
    showBack(): boolean {
      if (!this.$store.state.plannerUiState.isDesktop) {
        return false;
      }
      return this.$store.state.plannerUiState.sidebar.showsDetails;
    },
  },

  methods: {
    onCollapse() {
      this.$store.dispatch(PLANNER_UI_STATE_ACTIONS.SET_INTERACTIONS_EXPANDED, false);
    },
    onShowDisabledMessage() {
      // Currently unused, to see how it's used see: src/configurator/components/ConfiguratorSidebar.vue
      // this.$store.commit(PLANNER_UI_STATE_MUTATIONS.SET_OVERLAY_STATE, {overlay: OVERLAYS.ELEMENT_DISABLED, open: true});
    },
    onGoBack(_wasShowDetailsBefore: boolean) {
      const state = this.$store.state.plannerUiState.sidebar;
      this.$store.commit(COLLECTION_VIEW_STATE_MUTATIONS.ELEMENT_EXPANDED, {state, value: false});
      this.$store.commit(COLLECTION_VIEW_STATE_MUTATIONS.SET_SELECTED_ELEMENT, {state, value: null});
      // Currently unused, to see how it's used see: src/configurator/components/ConfiguratorSidebar.vue
    },
  },

});
