




































import AspectRatioBox from '@/configurator/components/utils/AspectRatioBox.vue';
import ImageWithFallback from '@/configurator/components/utils/ImageWithFallback.vue';
import NextAction from '@/common/components/side-bar/NextAction.vue';
import Vue from 'vue';
import {Nullable} from '@/common/utils/types';
import {getImageURL} from '@/common/utils/helper';

export default Vue.extend({
  components: {
    AspectRatioBox,
    ImageWithFallback,
    NextAction,
  },
  methods: {
    loadSelection() {
      this.$sdkConnector.loadSelectedPlanObject();
    },
  },
  computed: {
    perspectiveImage(): Nullable<string> {
      if (!this.$store.state.plannerUiState.selectionPayload) {
        return null;
      }
      const {configuration, item} = this.$store.state.plannerUiState.selectionPayload;
      const object = item || configuration;
      if (!object) {
        return null;
      }
      if (!object.perspectiveImage) {
        return null;
      }
      return getImageURL(object.perspectiveImage, 360);
    },
    selectionLabel(): Nullable<string> {
      if (!this.$store.state.plannerUiState.selectionPayload) {
        return null;
      }
      const {component, item} = this.$store.state.plannerUiState.selectionPayload;
      const object = item || component;
      if (!object) {
        return null;
      }
      return object.label;
    },
  },
});
