



















































import Vue from 'vue';
// @ts-ignore
import SvgSave from '@/common/svgs/save.svg';
// @ts-ignore
import SvgCart from '@/common/svgs/cart.svg';
import SvgIcon from '@/configurator/components/utils/SvgIcon.vue';
import {triggerRequestPlan} from '@/configurator/embedding/helper';
import {UI_BUTTON} from '@/configurator/embedding/types';
import {UI_STATE_MUTATIONS} from '@/configurator/store/ui-state';
import {OVERLAYS} from '@/configurator/components/-utils/overlays';

export default Vue.extend({
  components: {
    SvgIcon,
  },
  computed: {
    icons(): object {
      return {
        SvgSave,
        SvgCart,
      };
    },
    subLabel(): string {
      return 'Create your own set';
    },
    mainLabel(): string {
      return 'Multi Object Configurator';
    },
    showRequestPlanButton(): boolean {
      return true;
    },
  },
  methods: {
    async requestPlan() {
      await triggerRequestPlan(this.$embeddingCallbacks, this.$sdkConnector, this.$store, this.$analytics);
    },
    async saveDraft() {
      this.$embeddingCallbacks.onButtonClicked(UI_BUTTON.SAVEDRAFT);
      this.$store.commit(UI_STATE_MUTATIONS.SET_OVERLAY_STATE, {overlay: OVERLAYS.SAVE_DRAFT, open: true});
    },
  },
});
